<template>
    <section class="page-action">

        <div class="container-fluid p-0">
            <div class="row">
                <div class="col-md-12 padding-t_8">

                    <div class="theme-primary partition p-actions-expand tablehead">
                        <span class="p-name text-white">{{ $t("SetupGuides") }}</span>
                        <router-link to="/HelpAndGuide"><span class="p-actions"><a title=""
                                    class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip"
                                    data-original-title="Back">
                                    <i class="fa fa-angle-double-left"></i><br />{{ $t("Back") }}</a>

                            </span> </router-link>
                    </div>
                </div>
            </div>
        </div>

        <div class="row" style="margin-top: 10px;">

            <ul class="shortcuts-menu pb-3 connectedSortable">

                <li class="position-relative setupguides">
                    <a onmousedown="return false;" href="#" class="short-menu-item bg-white" data-toggle-tooltip="tooltip" data-original-title="Work Diary">
                        <span class="left-icon-m">
                            <i class="fa fa-calendar"></i>
                        </span>
                        <span class="short-item-name">Appointment Calendar</span>
                        <span class="left-icon-m barprogress">
                            <div class="Task mt-2">
                                    <div class="c100 small" v-bind:class="[Color, ('p' + Value)]">
                                        <span>{{Value}}%</span>
                                        <div class="slice">
                                            <div class="bar"></div>
                                            <div class="fill"></div>
                                        </div>
                                    </div></div>
                        </span>
                        <span class="left-icon-m tickposition d-none">
                            <i class="fa fa-check font-weight-light text-success font-14 mr-2"></i>
                        </span>
                        <span class="left-icon-m tickarrowposition">
                            <i class="fa fa-chevron-right font-weight-light text-dark font-14"></i>
                        </span>
                    </a>
                </li>
              
                <li class="position-relative setupguides">
                    <a onmousedown="return false;" href="#" class="short-menu-item bg-white" data-toggle-tooltip="tooltip" data-original-title="Work Diary">
                        <span class="left-icon-m">
                            <i class="fa fa-calendar"></i>
                        </span>
                        <span class="short-item-name">Appointment Calendar</span>
                        <span class="left-icon-m barprogress">
                            <div class="Task mt-2">
                                    <div class="c100 small" v-bind:class="[Color, ('p' + Value)]">
                                        <span>{{Value}}%</span>
                                        <div class="slice">
                                            <div class="bar"></div>
                                            <div class="fill"></div>
                                        </div>
                                    </div></div>
                        </span>
                        <span class="left-icon-m tickposition ">
                            <i class="fa fa-check font-weight-light text-success font-14 mr-2"></i>
                        </span>
                        <span class="left-icon-m tickarrowposition d-none">
                            <i class="fa fa-chevron-right font-weight-light text-dark font-14"></i>
                        </span>
                    </a>
                </li>


            </ul>




        </div>
    </section>
</template>

<script>

export default
    {
        name: 'SetupGuides',
        data() {
            return {

                Color: '',
                Value: 100,

            }
        },

        created: function () {

            if (this.Value <= 33) {
                this.Color = 'red'
            }
            else if (this.Value > 33 && this.Value <= 66) {
                this.Color = 'yellow'
            }
            else {
                this.Color = 'green'
            }

        },

    }

</script>





